.vacancyBlock {
  background-color: #d6e6f3;
  position: relative;
  height: 468px;
  margin: 0 -128px;
}

.points {
  margin: 0 -128px;
  padding: 72px 0;
  background-color: #ededed;
}

.title {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  color: #333333;
  margin-bottom: 40px;
}

.subTitle {
  font-size: 24px;
  font-weight: bold;
  line-height: 32px;
  color: #333333;
}

.pointsList {
  display: flex;
  padding: 0 128px;
  justify-content: space-between;
}

.point {
  width: 300px;
  padding-left: 28px;
  padding-top: 20px;
  margin-right: 52px;
}

.sellPoint {
  composes: point;
  border-left: 4px #2ca53a solid;
}

.helpPoint {
  composes: point;
  border-left: 4px #9b3ad8 solid;
}

.suggestPoint {
  composes: point;
  border-left: 4px #4497e3 solid;
}

.pointDescription {
  color: #333333;
  font-size: 17px;
  line-height: 24px;
  margin-top: 16px;
}

.vacancySection {
  position: absolute;
  top: 56px;
  left: 10vw;
}

.vacancyLabel {
  font-size: 46px;
  font-weight: bold;
  color: #333;
}

.vacancyDesc {
  font-size: 17px;
  color: #333333;
  margin-top: 16px;
  width: 528px;
}

.vacancyDesc span {
  font-weight: bold;
}

.vacancyPrice {
  margin-top: 20px;
  font-size: 17px;
  font-weight: bold;
  color: #1a9f29;
}

.vacancyButton {
  margin-top: 28px;
}

.listContainer {
  display: flex;
  justify-content: space-between;
  padding-bottom: 72px;
  flex: 1;
}

.listBlock {
  width: 528px;
  margin-right: 48px;
  margin-top: 40px;
}

.listHeader {
  height: 300px;
}

.listContent {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #f5f5f5;
  height: 520px;
  padding: 25px;
  margin-top: -5px;

}

.listTitle {
  font-size: 24px;
  font-weight: bold;
}

.listIn {
  margin-top: 24px;
  padding-left: 0;
}

.listIn li {
  margin-top: 20px;
  list-style: none;
}

.listIn li::before{
  content: '';
  display: inline-block;
  margin-right: 16px;
  width: 12px;
  height: 12px;
  background-image: linear-gradient(to bottom, #02d08f, #03b09a);
  border-radius: 100%;
}

.listOut {
  margin-top: 24px;
  padding-left: 0;
}

.listOut li {
  margin-top: 20px;
  list-style: none;
}

.listOut li::before{
  content: '';
  display: inline-block;
  margin-right: 16px;
  width: 12px;
  height: 12px;
  background-image: linear-gradient(to right, #25aff6, #2ecefa);
  border-radius: 100%;
}


.form {
  margin: 0 -128px;
  background: #d6e3ec;
  min-height: 676px;
  position: relative;
  padding: 76px 128px 85px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  box-sizing: border-box;
  z-index: 0;
}

.formField {
  margin-bottom: 16px;
}

.formInput {
  display: block;
  outline: none;
  width: 336px;
  height: 48px;
  background: #f2f2f2;
  border-radius: 4px;
  border: solid 1px #d7d7d7;
  padding-left: 12px;
  font-size: 17px;
  color: #333333;
}

.formSubmit {
  margin-top: 36px;
  margin-bottom: 20px;
  background-image: linear-gradient(to bottom, #02d08f, #03b09a);
}

.formTitle {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 12px;
}

.formDesc {
  margin-top: 12px;
}

.formControl {
  z-index: 1;
}

.vacancyGirl {
  height: 676px;
  position: absolute;
  bottom: 0;
  right: 128px;
}

.vacancySectionImage {
  position: absolute;
  right: 128px;
  height: 468px;
}

.fromTitle {
  font-size: 36px;
  font-weight: bold;
  color: #333333;
  line-height: 48px;
}

.formBlock {
  margin-top: 26px;
}

.error {
  margin-top: 5px;
  font-size: 13px;
  color: red;
}
