.title {
  font-size: 46px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.13;
  letter-spacing: normal;
  color: #333333;
  margin-bottom: 32px;
}

.subTitle {
  font-size: 24px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #333333;
  margin-bottom: 16px;
}

.agreement {
  font-size: 17px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.41;
  letter-spacing: normal;
  color: #333333;
  margin-top: 52px;
}

.list {
  list-style: none
}

.listItem {
  margin-bottom: 20px;
}

.listItem::before {
  content: '';
  display: inline-block;
  margin-right: 16px;
  width: 12px;
  height: 12px;
  background-image: linear-gradient(to bottom, #02d08f, #03b09a);
  border-radius: 100%;
}

.textBlock {
  margin: 32px 0;
}

.textBlockLast {
  margin-bottom: 120px;
}
