.stepsContainer {
  padding: 72px 0;
  margin: 0 -128px;
  background-color: #eeeefa;
}

.step {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  width: 240px;
  text-align: left;
  margin-right: 48px;
  z-index: 1;
}

.steps {
  display: flex;
  margin-top: 41px;
  font-size: 17px;
  color: #333333;
  line-height: 24px;
  padding: 0 128px;
  position: relative;
  z-index: 0;
}

.stepLine {
  background: url("~images/line.svg");
  width: 70%;
  max-width: 1000px;
  height: 2px;
  position: absolute;
  top: 30px;
  left: 0;
}

.title {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  color: #333333;
}

.stepIcon {
  height: 64px;
  margin-bottom: 23px;
}


.featuresContainer {
  margin-top: 72px;
  margin-bottom: 96px;
}

.features {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
  font-size: 17px;
  text-align: center;
  color: #333333;
  line-height: 24px;
}

.lie {
  width: 197px;
  margin-right: 24px;
  display: flex;
  flex-direction: column;
}

.bankFeature {
  height: 70px;
  margin-bottom: 39px;
}

.subText {
  margin-top: 20px;
}
