.vacancyLinks {
  display: none;
}

.footer {
  height: 116px;
  width: 100%;
  margin-top: auto;
  background: #252525;
  padding: 0 128px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footerYear {
  font-size: 17px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.41;
  letter-spacing: normal;
  color: #a8a8a8;
  margin-right: 12px;
}

.footerLink {
  font-size: 17px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.41;
  letter-spacing: normal;
  color: #ffffff;
}

.header {
  height: 110px;
  background: #fff;
  border-bottom: 1px solid rgba(136, 141, 148, 0.2);
  z-index: 1;
}

.headerTop {
  height: 60px;
  border-bottom: 1px solid rgba(136, 141, 148, 0.2);
  padding: 0 128px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.headerCity {
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: 0.6px;
  color: #333333;
  margin-left: 4px;
}

.headerLocation {
  display: flex;
  align-items: flex-start;
}

.changeLocationOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

.changeLocationModal {
  position: absolute;
  background: #fff;
  z-index: 4;
  top: 205px;
  left: 33%;
  width: 720px;
  padding: 32px;
  box-sizing: border-box;
  border-radius: 12px;
  box-shadow: 0 12px 36px 0 rgba(0, 0, 0, 0.5);
}

.locationModalCities {
  display: flex;
  flex-wrap: wrap;
}

.modalCityIcon {
  background: url("~images/ic-pin.svg") no-repeat;
  width: 24px;
  height: 24px;
  margin-right: 8px;
}

.modalCityName {
  font-size: 17px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.41;
  letter-spacing: normal;
  color: #333333;
}

.modalCityMain {
  font-size: 21px;
}

.modalCity {
  width: 45%;
  margin-right: 5%;
  margin-bottom: 36px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 40px;
}

.modalClose {
  background: url("~images/close.svg") no-repeat;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.modalTitle {
  font-size: 46px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.13;
  letter-spacing: normal;
  color: #333333;
}

.changeLocationBtn {
  cursor: pointer;
  font-size: 12px;
  color: #a0a09f;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
}

.content {
  overflow: auto;
  padding: 0 128px;
  background: #fff;
}

.navItems {
  display: flex;
  align-items: center;
  height: 50px;
  padding-left: 128px;
  position: relative;
}

.navItem {
  font-size: 12px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 2;
  letter-spacing: 0.5px;
  color: #333333;
  text-decoration: none;
  text-transform: uppercase;
  margin-right: 24px;
  padding: 12px 0;
  border-bottom: 4px solid transparent;
  cursor: pointer;
}

.navItemActive {
  border-bottom-color: #2ca53a;
}

.navDropdown {
  position: absolute;
  border-radius: 12px;
  box-shadow: 0 12px 36px 0 rgba(0, 0, 0, 0.5);
  background-color: #ffffff;
  top: 60px;
  padding: 24px;
  box-sizing: border-box;
}

.dropdownItem {
  margin-bottom: 20px;
}

.dropdownItemLink {
  font-size: 17px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.41;
  letter-spacing: normal;
  color: #333333;
  text-decoration: none;
}

.dropdownItem:last-child {
  margin-bottom: 0;
}
