.featureIcon {
  width: 104px;
  height: 104px;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
}

.stepIcon {
  width: 64px;
  height: 64px;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
}

.homeSection {
  width: 33%;
  margin: 92px 40px 50px 0;
}

.banner {
  display: flex;
  position: relative;
  justify-content: space-between;
  margin: 0 -128px;
  padding: 0 128px;
  background-color: #dce4e7;
  width: 100%;
}

.bannerTitle {
  font-size: 46px;
  font-weight: bold;
  color: #333333;
  line-height: 52px;
}

.bannerSubTitle {
  font-size: 17px;
  color: #333333;
  margin-top: 16px;
  width: 295px;
}

.joinButton {
  margin-top: 36px;
}

.title {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  color: #333333;
}

.homeImg {
  display: flex;
  align-items: flex-end;
}

.homeImg img {
  width: 100%;
  height: 100%;
}

.important {
  font-weight: bold;
}

.openVacancies {
  margin: 72px 0;
}

.vacancies {
  margin-top: 72px;
  display: flex;
  font-size: 17px;
  text-align: center;
  color: #333333;
  line-height: 24px;
  flex-wrap: wrap;
}

.position {
  padding: 24px;
  text-align: left;
}

.vacancyName {
  font-size: 24px;
  font-weight: bold;
  height: 48px;
  margin-bottom: 48px;
}

.vacancyDescription {
  margin-top: 12px;
  font-size: 17px;
  color: #333333;
}

.offerPrice {
  font-size: 17px;
  font-weight: bold;
  color: #1a9f29;
  margin-top: 20px;
}

.goInterview {
  margin-top: 24px;
}

.vacancy {
  margin-right: 48px;
  width: 336px;
  background-color: #f5f5f5;
}

.vacancyImage {
  height: 256px;
  margin-bottom: -5px;
}

